@import "variables";

.home-Container {
  display: flex;
  background: $body-bg1;
  overflow-x: hidden;
  min-height: 100vh;
  font-size: 1vw;

  hr {
    height: 1px;
    color: #424242;
  }

  .home {
    position: relative;
    left: 92px;
    margin-right: 92px;
    min-height: fit-content;
    background: $body-bg2;
    width: auto;

    .flex-split {
      display: flex;
      padding: 25px 0 0 0;

      h2 {
        width: 200px;
        color: $appWhite;
        font-weight: 500;
        font-size: 22px;
      }

      li {
        list-style: none;
        padding-right: 1%;
        margin-left: auto;
      }
    }

    .homeData {
      display: flex;
      justify-content: space-between;
      padding: 0;
    }
  }
}


.homeRightSide {

  width: 90%;
  padding: 30px;

  .flex-row-list {
    font-size: 1vw;
    color: $offWhite;
    width: auto;
    display: flex;
    list-style: none;
    padding-bottom: 10px;

    li {
      color: $offWhite;
      padding: 5px 20px 10px 0;
    }

    .toRight {
      display: flex;
      margin-left: auto;

      li {
        display: flex;
        color: $offWhite;
      }
    }
  }

  h2 {
    color: $appWhite;
    text-align: left;
    font-weight: 400;
    font-size: 3vh;
    padding: 2.1% 0 0 0;
  }

  .flex-row-home {
    width: 100%;
    display: flex;
    padding: 12px 12px 12px 0;

    li {
      list-style: none;
      color: $appWhite;
      font-weight: 300;
    }
  }

  .orderList {
    max-height: 57vh;
    width: auto;
    overflow-y: scroll;
    scrollbar-width: none;
  }

  .orderSubmitTab {
    padding: 10px 0 0 0;
    text-align: left;
    width: 100%;

    p {
      color: $offWhite;
      font-weight: 300;
      padding-top: 10px;
    }

    span {
      float: right;
      margin-right: 10px;
      font-weight: 700;
    }
  }
}

.topSection {
  padding: 5px 30px 0 25px;
  height: 120px;
  text-align: left;

  .dropDownIcon {
    display: none;

  }

  .hide {
    display: none;
  }

  h1 {
    font-size: 25px;
    color: $appWhite;
    font-weight: 500;
  }

  h2 {
    padding-top: 5px;
    color: $offWhite;
    font-size: 14px;
    font-weight: 500;
  }

  h3 {
    padding: 30px 30px 30px 0;
    color: $appWhite;
    font-size: 18px;
    font-weight: 400;
  }

  .rightSide {
    float: right;

    button {
      cursor: pointer;
      margin-top: 20px;
      border-radius: 8px;
      background: $body-bg1;
      color: $appWhite;
      border-style: ridge;
      border-width: thin;
      border-color: #68696c;
      padding: 10px;
    }

    button:hover {
      color: $theme;
      opacity: .8;
    }

    input {
      margin-top: 10px;
      background: #2D303E;
      padding: 10px;
      border-radius: 8px;
      border-style: outset;
      border-color: #2D303E;
      color: $appWhite;
    }
  }

  .header {
    padding: 20px 0 0 0;
    margin-left: -40px;

    .headerUl {
      display: flex;
      justify-content: space-between;
      max-width: 40vw;

      .activeLink {
        a {
          text-decoration: none;
          color: $theme;
          font-weight: 600;
        }

        hr {
          margin-top: 20px;
          color: #EA7C69;
          width: 35px;
        }
      }

      .inActiveLink {
        a {
          text-decoration: none;
          color: $appWhite;
          font-weight: 300;
        }

        hr {
          display: none;
        }
      }
    }
  }
}

@media screen and (min-width: 1920px) {
  .home-Container {
    .home {
      .topSection {
        height: 10.5vh;
      }
    }
  }
}

@media screen and (min-width: 3840px) {
  .home-Container {
    .home {
      .topSection {
        height: 6vh;

        .header {
          padding: 20px 0 0 0;
          margin-left: -40px;

          .headerUl {
            display: flex;
            justify-content: space-between;
            max-width: 40vw;

            .activeLink {
              a {
                text-decoration: none;
                color: $theme;
                font-weight: 600;
                font-size: 160%;
              }

              hr {
                margin-top: 20px;
                color: #EA7C69;
                width: 35px;
              }
            }

            .inActiveLink {
              a {
                font-size: 150%;
                text-decoration: none;
                color: $appWhite;
                font-weight: 300;
              }

              hr {
                display: none;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 800px) {

  .home-Container {

    hr {
      height: 1px;
      color: #424242;
    }

    .home {
      left: 0px;
      margin-right: 0;
      width: 100%;

      .flex-split {

        h2 {
          display: none;
        }

        li {
          list-style: none;
          margin-left: 85%;
        }
      }

      .topSection {
        padding: 10px;

        .dropDownIcon {
          display: block;
          cursor: pointer;
          margin-top: 25px;

          span {
            color: $offWhite;
            font-size: 12px;
          }
        }

        h1 {
          font-size: 15px;
          width: 210px;
        }

        h2 {
          font-size: 10px;
        }

        .rightSide {
          padding-right: 0;

          input {
            width: 70px;
            margin-top: 0;
            padding: 5px;
            border-style: ridge;
          }
        }

        .header {
          display: none;
        }

        .header-mobile {
          display: flex;
          background: $body-bg1;
          width: 170px;
          border-bottom-right-radius: 90px;
          margin: 0 0 0 -40px;
          position: relative;
          z-index: 100;

          .headerUl {

            display: flex;
            flex-direction: column;
            min-width: fit-content;

            .activeLink {
              a {
                text-decoration: none;
                color: $theme;
                font-size: 18px;
                padding: 10px;

              }

              hr {
                margin-bottom: -25px;
                width: 35px;
                color: $theme;
                margin-left: 7px;

              }
            }

            .inActiveLink {
              a {
                font-size: 15px;
                text-decoration: none;
                color: $appWhite;
                font-weight: 300;
                padding: 10px;
              }

              hr {
                display: none;
              }
            }
          }
        }
      }

      .homeData {
        padding: 0;
      }

      .content {
        background: transparent;
        padding: 12px;
        min-height: 250px;
      }

      .column {
        width: 100%;
        opacity: 1;
      }
    }

    .homeRightSide {
      display: none;
      padding: 3px;
      width: 34vw;

      h2 {
        font-size: 13px;
        padding: 5px;
      }

      .flex-row {
        padding: 5px;
        flex-direction: column;

        li {
          margin: 5px 0 0 5px;
          font-size: 9px;
        }

        .toRight {
          float: right;

          li {
            display: none;
            padding-left: 0;
          }
        }
      }

      .orderSubmitTab {
        padding: 2px;

        p {
          font-size: 11px;
        }

        span {
          float: right;
          margin-right: 10%;
        }
      }
    }

  }


}
